nav {
  display : flex;
  justify-content: center;
  gap: 30px;
  margin-top: 25px;

  padding: 10px 20px; /* Padding for the 'box' around the text */
  border-radius: 10px; /* Rounded corners for the box */
  background: rgba(255, 255, 255, 0.1); /* Slight white background */
  backdrop-filter: blur(10px); /* Apply the blur effect */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow to make it stand out */
  transition: all 0.3s ease; /* Smooth transition on hover or click */

}

ul {
  list-style: none; /* 기본적으로 ul의 점을 제거 */
  padding: 0; /* 기본 padding 제거 */
  margin: 0; /* 기본 margin 제거 */
  display: flex; /* <ul>도 flex로 설정하여 항목을 수평 배치 */
}

li {
  display: inline; /* <li> 항목을 인라인으로 설정 */
  margin: 0 60px;
}

.nav-link{
  text-decoration:s none;
  color :rgb(255, 255, 255);
  font-size: 20px;

}
.nav-link.click{
  font-weight: bold;
  color:rgb(166, 212, 236);

}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.2); /* Slightly more visible background on hover */
  backdrop-filter: blur(12px); /* Slightly stronger blur on hover */
}